
.price-page {
  .price-list {
    text-align: center;
    h1 {
      margin-bottom: 40px;
    }
    p {
      display: flex;
      border-bottom: 1px solid #eee;
      span {
        flex: 1;
        font-size: 18px;
        line-height: 34px;
        margin: 10px 0;
      }
    }
    .title {
      span {
        font-size: 24px;
        line-height: 40px;
      }
    }
  }
}