
.passcode-page {
  font-family: 'Montserrat', sans-serif;
  p {
    font-weight: bold;
    text-align: center;
  }
  .time {
    margin-top: 60px;
    color: #ea5535;
    font-size: 16px;
    margin-top: 40px;
    margin-bottom: 0;
  }
  .title {
    font-size: 16px;
    margin: 10px 0 10px;
    color: #363636;
  }
  .name {
    color: #008eff;
    font-size: 16px;
    margin-top: 30px;
    margin-bottom: 0;
    display: flex;
    span {
      flex: 1;
      text-align: center;
    }
  }
  .tip {
    color: #343434;
    // margin-top: 10px;
    font-size: 16px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    .tip-box {
      p {
        margin: 0;
        text-align: left;
      }
    }
  }
  .back-btn {
    color: #fff;
    width: 200px;
    padding: 8px 24px;
    font-size: 14px;
    text-align: center;
    margin: 0 auto 20px;
    border-radius: 4px;
    background-color: #07c160;
  }
  img {
    width: 50%;
    display: block;
    margin: 0 auto;
  }
  .nav {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    img {
      width: 100%;
    }
  }
}