
.testflight-page {
  .advantage {
    color: #3b566e;
    text-align: center;
    h1 {
      margin-bottom: 40px;
    }
    .img {
      display: block;
      width: 180px;
      height: 180px;
      margin: 0 auto;
    }
    .img-01 {
      background: url(./../../images/testflight/home-03.png) no-repeat center center;
      background-size: contain;
    }
    .img-02 {
      background: url(./../../images/testflight/home-04.png) no-repeat center center;
      background-size: contain;
    }
    .img-03 {
      background: url(./../../images/testflight/home-05.png) no-repeat center center;
      background-size: contain;
    }
    .img-04 {
      background: url(./../../images/testflight/home-06.png) no-repeat center center;
      background-size: contain;
    }
    h2 {
      margin-top: 20px;
      line-height: 40px;
      font-weight: 400;
    }
    p {
      font-size: 16px;
      line-height: 34px;
      margin-top: 20px;
    }
  }

  .certificate {
    padding: 90px 0;
    background: #f7fafc;
    overflow: hidden;
    img {
      height: 502px;
      display: block;
      margin: 0 auto;
    }
    h1 {
      font-weight: 500;
      font-size: 24px;
      color: #3B566E;
      margin-top: 120px;
      margin-bottom: 20px;
    }
    h2 {
      font-weight: 500;
      font-size: 20px;
      color: #3B566E;
    }
    p {
      font-size: 15px;
      font-weight: 500; 
      color: rgb(111, 139, 164);
    }
  }
}
