
.banner {
  height: calc(100vh - 76px);
  background: linear-gradient(90deg,#2c2adf,#56defa);
  overflow: hidden;
  .container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .info {
    width: 520px;
    padding-top: 160px;
    h1 {
      color: #fff;
      font-size: 64px;
      font-weight: 500;
      margin: 0 0 20px;
    }
    p {
      color: #eee;
      font-size: 18px;
      padding: 0 0 40px;
      margin: 0;
    }
  }
  img {
    width: 110%;
  }
}

@media (max-width: 992px) {
  .banner {
    .info {
      // padding-top: 480px;
    }
  }
}
@media (max-width: 768px) {
  .navbar .navbar-collapse .navbar-nav {
    width: 100%;
  }
}
@media (max-width: 542px) {
  .banner .info {
    width: 100%;
  }
}
