      display: block;
      width: 80px;
      height: 80px;
      margin: 0 auto;
    }
    .img-01 {
      background: url(./../../images/conpanysign/s-1.png) no-repeat center center;
      background-size: contain;
    }
    .img-02 {
      background: url(./../../images/conpanysign/s-2.png) no-repeat center center;
      background-size: contain;
    }
    .img-03 {
      background: url(./../../images/conpanysign/s-3.png) no-repeat center center;
