
.home-page {
  .service {
    color: #3b566e;
    h1 {
      text-align: center;
      margin-bottom: 40px;
    }
    .img {
      display: block;
      width: 180px;
      height: 180px;
      margin: 0 auto;
    }
    .img-01 {
      background: url('./../../images/home/home-03.png') no-repeat center center;
      background-size: contain;
    }
    .img-02 {
      background: url('./../../images/home/home-04.png') no-repeat center center;
      background-size: contain;
    }
    .img-03 {
      background: url('./../../images/home/home-05.png') no-repeat center center;
      background-size: contain;
    }
    .img-04 {
      background: url('./../../images/home/home-06.png') no-repeat center center;
      background-size: contain;
    }
    h2 {
      text-align: center;
      margin-top: 20px;
      line-height: 40px;
      font-weight: 400;
    }
    p {
      text-align: center;
      font-size: 16px;
      line-height: 34px;
      margin-top: 20px;
    }
  }

  .certificate {
    padding: 0 0 90px 0;
    background: #f7fafc;
    img {
      width: 100%;
      display: block;
      margin: 0 auto;
    }
    h1 {
      font-weight: 500;
      font-size: 24px;
      color: #3B566E;
      letter-spacing: 1.30px;
      margin-top: 160px;
      margin-bottom: 20px;
    }
    .text {
      font-size: 16px;
      width: 400px;
      max-width: 100%;
      color: rgb(111, 139, 164);
    }
    .tips {
      p {
        font-size: 18px;
        font-weight: 500; 
        color: rgb(111, 139, 164);
      }
    }
  }

  .advantage {
    color: #3B566E;
    text-align: center;
    h1 {
      margin-bottom: 40px;
    }
    .img {
      display: block;
      width: 80px;
      height: 80px;
      margin: 0 auto;
    }
    .img-01 {
      background: url('./../../images/home/superimg_1.png') no-repeat center center;
      background-size: contain;
    }
    .img-02 {
      background: url('./../../images/home/superimg_2.png') no-repeat center center;
      background-size: contain;
    }
    .img-03 {
      background: url('./../../images/home/superimg_3.png') no-repeat center center;
      background-size: contain;
    }
    .img-04 {
      background: url('./../../images/home/superimg_4.png') no-repeat center center;
      background-size: contain;
    }
    h2 {
      margin-top: 20px;
      line-height: 40px;
      font-weight: 400;
    }
    p {
      font-size: 16px;
      line-height: 34px;
      margin-top: 20px;
    }
  }
}
